.wrapper{
    /* background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,121,31,1) 35%, rgba(0,212,255,1) 100%); */
    display: flex;
    justify-content: center;
    align-items: center;
    border: 0;
    border-radius: 20px;
    margin-top: 20px;
}
.full {
    width: 70%;
    max-width: 1200px;
    min-height: 100px;
    background-color: rgb(245, 239, 231);
    margin: 0px;
    display: grid;
    grid-template-columns: 2fr 4fr;
}
.left {
    position: initial;
    padding: 20px;
    border-right: 4px solid;
 
}
.right {
    position: initial;
    padding: 20px;
 
}
.image, .Contact, .Skills, .Language, .Hobbies, .title,
.Summary, .Experience, .Education, .project {
    margin-bottom: 30px;
}
.title p {
    font-size: 25px;
}
.h2 {
    background-color: rgb(4, 96, 150);
}

.ex-link {
    text-decoration: none;
    color: black;
}

.ex-link:hover {
    text-decoration: underline;
    color: brown;
}